<script>
import GxLedgerAccountsList from '@components/_CoreTaxes/LedgerAccounts/GxLedgerAccountsList'
import { LEDGER_ACCOUNT_DELETE } from '@src/apollo/mutations'

export default {
  name: 'GxDeleteLedgerAccount',
  components: { GxLedgerAccountsList },
  data() {
    return {
      errorDescription: '',
      loading: false,
      ledgerAccount: null,
      accountingFirmNumber: null,
      clientNumber: null,
    }
  },
  created() {
    this.ledgerAccount = this.deleteLedgerAccountProp
    this.accountingFirmNumber = this.accountingFirmNumberProp
    this.clientNumber = this.clientNumberProp
    //this.ledgerAccount.subAccounts = null
    //console.log(this.ledgerAccount)
  },
  props: {
    deleteLedgerAccountProp: {
      type: Object,
      default: null,
    },
    accountingFirmNumberProp: {
      type: String,
      default: null,
    },
    clientNumberProp: {
      type: String,
      default: null,
    },
  },
  methods: {
    async onSubmit() {
      this.loading = true
      try {
        // console.log(this.accountingFirmNumber)
        // console.log(this.clientNumber)
        // console.log(this.ledgerAccount.ledgerAccountId)
        const response = await this.$apollo.mutate({
          mutation: LEDGER_ACCOUNT_DELETE,
          variables: {
            accountingFirmNumber: this.accountingFirmNumber,
            clientNumber: this.clientNumber,
            ledgerAccountId: this.ledgerAccount.ledgerAccountId,
          },
        })
        console.log(response)
        this.loading = false
        //this.$emit('onDelete', this.ledgerAccount.ledgerAccountId)
        if (
          response.data.ledgerAccountDelete.successful &&
          response.data.ledgerAccountDelete.error === null
        ) {
          this.$emit('onDelete', this.ledgerAccount.ledgerAccountId)
        } else {
          this.errorDescription =
            response.data.ledgerAccountDelete.error.message +
            ' - ' +
            response.data.ledgerAccountDelete.error.details.join('')
        }
      } catch (error) {
        this.errorDescription = this.$utils.parseServerError(error)
        console.log(this.errorDescription)
        this.loading = false

        return null
      }
      //this.$emit('onCloseDelete')
    },
    onCloseDelete() {
      this.$emit('onCloseDelete')
    },
  },
}
</script>

<template>
  <GxForm class="flex flex-col font-nunito" @submit="onSubmit">
    <div
      class="flex flex-col w-98 h-120 border p-6 bg-white rounded-lg items-center"
    >
      <div
        class="flex flex-col w-44 h-44"
        style="background-image: url('/img/de_002_bienvenido_temporal.776ac71d.svg'); background-position: right center;"
      >
      </div>
      <div class="flex flex-col mt-3 text-center">
        <span class="text-2xl font-bold"
          >Ests seguro que quieres eliminar permanentemente esta cuenta
          contable?</span
        >
        <div class="flex flex-col mt-2 ml-4 items-center" v-if="ledgerAccount">
          <GxLedgerAccountsList
            :ledgerAccountsProp="[ledgerAccount]"
            :editCatalog="false"
            :showCollapsed="true"
            :showArrowCollapse="false"
            :showHover="false"
            :justThisItem="true"
          />
          <div class="ml-5 mt-2 text-center" v-if="this.errorDescription">{{
            this.errorDescription
          }}</div>
        </div>
        <!-- <span class="mt-5 text-sm">Numero y nombre de la cuenta</span> -->
      </div>
      <div class="flex w-3/4 absolute mt-96">
        <div class="flex fixed">
          <div
            class="flex flex-col mr-4"
            v-show="!this.loading"
            :class="{ ' delay-300 duration-300 hidden': this.loading }"
          >
            <GxButton
              :variant="true"
              :ghost="true"
              class=" bottom-0 rounded-md gx-button"
              @click="onSubmit"
            >
              SI, ELIMINARLA
            </GxButton>
          </div>
          <div
            class="flex flex-col"
            :class="{ ' delay-300 duration-300 pl-36': this.loading }"
          >
            <GxButton
              :variant="!this.loading"
              class="bottom-0 gx-button"
              type="submit"
              :loading="loading"
              @click="onCloseDelete"
            >
              NO ELIMINARLA
            </GxButton>
          </div>
        </div>
      </div>

      <!-- <div class="flex absolute mt-96">
        <GxButton
          :variant="!this.loading"
          :ghost="!this.loading"
          class="ml-4 bottom-0 rounded-md gx-button"
          :loading="loading"
          @click="onSubmit"
        >
          SI, Eliminarla
        </GxButton>
        <GxButton
          :variant="!this.loading"
          class="ml-2 bottom-0 gx-button"
          @click="onCloseDelete"
        >
          NO ELIMINARLA
        </GxButton>
      </div> -->
    </div>
  </GxForm>
</template>
