<script>
import GxLedgerAccountsListItem from '@components/_CoreTaxes/LedgerAccounts/GxLedgerAccountsListItem'

export default {
  name: 'GxLedgerAccountsList',
  components: {
    GxLedgerAccountsListItem,
  },
  data() {
    return {
      accountingFirmNumber: null,
      clientNumber: null,
    }
  },
  props: {
    ledgerAccountsProp: {
      type: Array,
      default: null,
    },
    editCatalog: {
      type: Boolean,
      default: false,
    },
    editBeginningBalances: {
      type: Boolean,
      default: false,
    },
    showCollapsed: {
      type: Boolean,
      default: false,
    },
    showArrowCollapse: {
      type: Boolean,
      default: false,
    },
    showHover: {
      type: Boolean,
      default: true,
    },
    justThisItem: {
      type: Boolean,
      default: false,
    },
    accountingFirmNumberProp: {
      type: String,
      default: null,
    },
    clientNumberProp: {
      type: String,
      default: null,
    },
    isRootProp: {
      type: Boolean,
      default: false,
    },
    isInitial: {
      type: Boolean,
      default: false,
    },
  },
  created() {
    this.accountingFirmNumber = this.accountingFirmNumberProp
    this.clientNumber = this.clientNumberProp
  },
  computed: {
    ledgerAccounts() {
      return this.ledgerAccountsProp
    },
  },
  watch: {
    immediate: true,
  },
  mounted() {
    if (this.isRootProp) this.$emit('finishRender')
  },
  methods: {
    editLedgerAccount(ledgerAccountParent) {
      this.$emit('editLedgerAccount', ledgerAccountParent)
    },
    createSubLedgerAccount(ledgerAccountParent) {
      // console.log(ledgerAccountParent)
      this.$emit('createSubLedgerAccount', ledgerAccountParent)
    },
    recalculatedParent(amount, childrenNumber) {
      this.$emit('recalculatedParent', amount, childrenNumber)
      //this.beginningBalance += amount
    },
    deleteLedgerAccount(deleteLedgerAccount) {
      this.$emit('deleteLedgerAccount', deleteLedgerAccount)
    },
  },
}
</script>

<template>
  <ul class="font-nunito">
    <GxLedgerAccountsListItem
      :accountingFirmNumberProp="accountingFirmNumber"
      :clientNumberProp="clientNumber"
      v-for="(item, index) in ledgerAccounts"
      :key="index"
      :childrenNumberProp="index"
      :ledgerAccountProp="item"
      :editCatalog="editCatalog"
      :editBeginningBalances="editBeginningBalances"
      :showCollapsed="showCollapsed"
      :showArrowCollapse="showArrowCollapse"
      :showHover="showHover"
      @editLedgerAccount="editLedgerAccount"
      @createSubLedgerAccount="createSubLedgerAccount"
      @deleteLedgerAccount="deleteLedgerAccount"
      @recalculatedParent="recalculatedParent"
      :justThisItem="justThisItem"
      :isInitial="isInitial"
    />
  </ul>
</template>
