  created() {
    this.accountingFirmNumber = this.accountingFirmNumberProp
    this.clientNumber = this.clientNumberProp
    if (this.ledgerAccountEdit.additionalDataLocalized !== null)
      this.setsatGroupCodeOption(
        this.ledgerAccountEdit.additionalDataLocalized.codigoAgrupadorDelSat
      )
    this.setTypeTranslate(
      this.ledgerAccountEdit.ledgerAccountType.type,
      this.ledgerAccountEdit.ledgerAccountType.subType
    )
    //console.log(this.ledgerAccountEdit)
    //this.ledgerAccount = this.ledgerAccountEdit
    //this.ledgerAccountsParents = this.ledgerAccountsParentsProp
    // console.log(this.ledgerAccountsParents)
  },
  computed: {
    getLavelNumber() {
      return this.isRoot ? 'Numero de la cuenta mayor' : 'Numero de la cuenta'
    },
    ledgerAccountsParents() {
      return this.ledgerAccountsParentsProp
    },
    ledgerAccountEdit() {
      return this.getledgerAccountEdit(this.ledgerAccountsParentsProp[0])
    },
    typeParent() {
      for (
        var index = 0;
        index < this.ledgerAccountsParentsProp.length;
        index++
      ) {
        return this.getTypeTranslate(
          this.ledgerAccountsParentsProp[index].ledgerAccountType.type,
          this.ledgerAccountsParentsProp[index].ledgerAccountType.subType
        )
      }
      return ''
    },
    natureParent() {
      for (
        var index = 0;
        index < this.ledgerAccountsParentsProp.length;
        index++
      ) {
        return this.ledgerAccountsParentsProp[index].nature === 'debit'
          ? 'Debito'
          : 'Credito'
      }
      return ''
      //return 'Activo - Circulante'
    },
  },
  props: {
    isRoot: {
      type: Boolean,
      default: true,
    },
    accountingFirmNumberProp: {
      type: String,
      default: null,
    },
    clientNumberProp: {
      type: String,
      default: null,
    },
    ledgerAccountsParentsProp: {
      type: Array,
      default: null,
    },
  },
  components: {
    GxDropdown,
    GxLedgerAccountsList,
  },
  methods: {
    setsatGroupCodeOption(codeSatGroup) {
      // console.log(codeSatGroup)
      if ((codeSatGroup ?? '') !== '') {
        this.satGroupCode.forEach((code) => {
          if (code.indexOf(codeSatGroup + ' - ') > -1)
            this.satGroupCodeOption = code
        })
      }
    },
    setTypeTranslate(type, subType) {
      switch ((type, subType)) {
        case ('assets', 'currentAssets'):
          this.typeOption = 'Activo circulante'
          break
        case ('assets', 'noncurrentAssets'):
          this.typeOption = 'Activo no circulante'
          break

        case ('assets', 'deferredAssets'):
          this.typeOption = 'Activo diferido'
          break

        case ('liabilities', 'currentLiabilities'):
          this.typeOption = 'Pasivo circulante'
          break

        case ('liabilities', 'noncurrentLiabilities'):
          this.typeOption = 'Pasivo no circulante'
          break
        case ('liabilities', 'deferredLiabilities'):
          this.typeOption = 'Pasivo diferido'
          break
        case ('capital', 'equity'):
          this.typeOption = 'Capital'
          break
        case ('capital', 'retainedEarnings'):
          this.typeOption = 'Ganancias retenidas'
          break
        case ('revenue', 'sales'):
          this.typeOption = 'Ventas'
          break
        case ('revenue', 'otherIncome'):
          this.typeOption = 'Otros ingresos'
          break
        case ('expenses', 'costofGoodsSold'):
          this.typeOption = 'Costo de ventas'
          break
        case ('expenses', 'expenses'):
          this.typeOption = 'Gastos'
          break
        case ('expenses', 'depreciation'):
          this.typeOption = 'Depreciacin'
          break
        case ('expenses', 'otherExpenses'):
          this.typeOption = 'Otros gastos'
          break
        case ('order', 'order'):
          this.typeOption = 'Orden'
          break
        default:
          this.typeOption = ''
          break
      }
    },
    getledgerAccountEdit(ledgerAccountSubAccount) {
      if ((ledgerAccountSubAccount.subAccounts ?? []).length > 0)
        return this.getledgerAccountEdit(ledgerAccountSubAccount.subAccounts[0])
      else return ledgerAccountSubAccount
    },
    getTypeTranslate(type, subType) {
      switch ((type, subType)) {
        case ('assets', 'currentAssets'):
          return 'Activo - Circulante'
        case ('assets', 'noncurrentAssets'):
          return 'Activo - No circulante'
        case ('assets', 'deferredAssets'):
          return 'Activo - Diferido'
        case ('liabilities', 'currentLiabilities'):
          return 'Pasivo - Circulante'
        case ('liabilities', 'noncurrentLiabilities'):
          return 'Pasivo - No circulante'
        case ('liabilities', 'deferredLiabilities'):
          return 'Pasivo - Diferido'
        case ('capital', 'equity'):
          return 'Capital'
        case ('capital', 'retainedEarnings'):
          return 'Ganancias retenidas'
        case ('revenue', 'sales'):
          return 'Ventas'
        case ('revenue', 'otherIncome'):
          return 'Otros ingresos'
        case ('expenses', 'costofGoodsSold'):
          return 'Costo de ventas'
        case ('expenses', 'expenses'):
          return 'Gastos'
        case ('expenses', 'depreciation'):
          return 'Depreciacin'
        case ('expenses', 'otherExpenses'):
          return 'Otros gastos'
        case ('order', 'order'):
          return 'Orden'
        default:
          return ''
      }
    },
    onCloseEdit() {
      this.$emit('onCloseEdit')
      this.reset()
    },
    fillDataParent(parent) {
      if (parent[0].ledgerAccountId === this.ledgerAccountEdit.parent) {
        this.ledgerAccountEdit.number =
          parent[0].number + '-' + this.ledgerAccountEdit.shortNumber
        return
      } else {
        this.fillDataParent(parent[0].subAccounts)
      }
    },
    async EditLedgerAccount() {
      this.loading = true
      try {
        if (this.ledgerAccountEdit.parent !== null) {
          this.fillDataParent(this.ledgerAccountsParents)
        } else {
          this.ledgerAccountEdit.number = this.ledgerAccountEdit.shortNumber
        }
        // console.log({
        //   accountingFirmNumber: this.accountingFirmNumber,
        //   clientNumber: this.clientNumber,
        //   ledgerAccountId: this.ledgerAccountEdit.ledgerAccountId,
        //   input: {
        //     number: this.ledgerAccountEdit.number,
        //     name: this.ledgerAccountEdit.name,
        //     type: this.ledgerAccountEdit.ledgerAccountType.type,
        //     subType: this.ledgerAccountEdit.ledgerAccountType.subType,
        //     subSubType: this.ledgerAccountEdit.ledgerAccountType.subSubType,
        //     nature: this.ledgerAccountEdit.nature,
        //     additionalDataLocalized: {
        //       codigoAgrupadorDelSat: this.ledgerAccountEdit
        //         .additionalDataLocalized.codigoAgrupadorDelSat,
        //     },
        //   },
        // })
        const response = await this.$apollo.mutate({
          mutation: LEDGER_ACCOUNT_UPDATE,
          variables: {
            accountingFirmNumber: this.accountingFirmNumber,
            clientNumber: this.clientNumber,
            ledgerAccountId: this.ledgerAccountEdit.ledgerAccountId,
            input: {
              number: this.ledgerAccountEdit.number,
              name: this.ledgerAccountEdit.name,
              type: this.ledgerAccountEdit.ledgerAccountType.type,
              subType: this.ledgerAccountEdit.ledgerAccountType.subType,
              subSubType: this.ledgerAccountEdit.ledgerAccountType.subSubType,
              nature: this.ledgerAccountEdit.nature,
              additionalDataLocalized: {
                codigoAgrupadorDelSat: this.ledgerAccountEdit
                  .additionalDataLocalized.codigoAgrupadorDelSat,
              },
            },
          },
        })
        //console.log(response)
        if (
          response.data.ledgerAccountUpdate.ledgerAccount !== null &&
          response.data.ledgerAccountUpdate.error === null
        ) {
          this.$emit(
            'onUpdate',
            response.data.ledgerAccountUpdate.ledgerAccount
          )
        } else {
          this.errorDescription =
            response.data.ledgerAccountUpdate.error.message +
            ' - ' +
            response.data.ledgerAccountUpdate.error.details.join('')
        }
        this.loading = false
      } catch (error) {
        //console.log(error)
        this.loading = false
        this.errorDescription = this.$utils.parseServerError(error)
        // this.$emit('notificationError', this.errorDescription)
        return null
      }

      //console.log(this.ledgerAccountEdit)
      //ledgerAccountEdit
    },
    onSubmit() {
      this.$emit('onCloseEdit')
      this.reset()
    },
    changeNature(nature) {
      //console.log(nature)
      this.ledgerAccountEdit.nature = nature
    },
    reset() {
      // this.ledgerAccount = {
      //   ledgerAccountId: null,
      //   number: '',
      //   name: '',
      //   type: '',
      //   subType: '',
      //   subSubType: '',
      //   nature: 'debit',
      //   additionalDataLocalized: {
      //     codigoAgrupadorDelSat: '',
      //   },
      // }
      this.loading = false
      this.loadingSuccess = false
    },
  },
  watch: {
    errorDescription: {
      handler(newVal) {
        if (newVal) {
          if (this.errorDescription) {
            let timeout = setTimeout(() => {
              this.errorDescription = ''
            }, 4000)

            this.$once('hook:beforeDestroy', () => {
              clearTimeout(timeout)
            })
          }
        }
      },
    },
    satGroupCodeOption: {
      handler(newVal) {
        if (newVal) {
          // console.log(newVal)
          // console.log(newVal.substring(0, newVal.indexOf('-') - 1))
          if (this.ledgerAccountEdit.additionalDataLocalized === null) {
            this.ledgerAccountEdit.additionalDataLocalized = {
              codigoAgrupadorDelSat: '',
            }
          }
          this.ledgerAccountEdit.additionalDataLocalized.codigoAgrupadorDelSat = newVal.substring(
            0,
            newVal.indexOf('-') - 1
          )
        }
      },
    },
    typeOption: {
      handler(newVal) {
        if (newVal) {
          this.ledgerAccountEdit.ledgerAccountType.subSubType = null
          switch (newVal) {
            case 'Activo circulante':
              this.ledgerAccountEdit.ledgerAccountType.type = 'assets'
              this.ledgerAccountEdit.ledgerAccountType.subType = 'currentAssets'
              break
            case 'Activo no circulante':
              this.ledgerAccountEdit.ledgerAccountType.type = 'assets'
              this.ledgerAccountEdit.ledgerAccountType.subType =
                'noncurrentAssets'
              break
            case 'Activo diferido':
              this.ledgerAccountEdit.ledgerAccountType.type = 'assets'
              this.ledgerAccountEdit.ledgerAccountType.subType =
                'deferredAssets'
              break
            case 'Pasivo circulante':
              this.ledgerAccountEdit.ledgerAccountType.type = 'liabilities'
              this.ledgerAccountEdit.ledgerAccountType.subType =
                'currentLiabilities'
              break
            case 'Pasivo no circulante':
              this.ledgerAccountEdit.ledgerAccountType.type = 'liabilities'
              this.ledgerAccountEdit.ledgerAccountType.subType =
                'noncurrentLiabilities'
              break
            case 'Pasivo diferido':
              this.ledgerAccountEdit.ledgerAccountType.type = 'liabilities'
              this.ledgerAccountEdit.ledgerAccountType.subType =
                'deferredLiabilities'
              break
            case 'Capital':
              this.ledgerAccountEdit.ledgerAccountType.type = 'capital'
              this.ledgerAccountEdit.ledgerAccountType.subType = 'equity'
              break
            case 'Ganancias retenidas':
              this.ledgerAccountEdit.ledgerAccountType.type = 'capital'
              this.ledgerAccountEdit.ledgerAccountType.subType =
                'retainedEarnings'
              break
            case 'Ventas':
              this.ledgerAccountEdit.ledgerAccountType.type = 'revenue'
              this.ledgerAccountEdit.ledgerAccountType.subType = 'sales'
              break
            case 'Otros ingresos':
              this.ledgerAccountEdit.ledgerAccountType.type = 'revenue'
              this.ledgerAccountEdit.ledgerAccountType.subType = 'otherIncome'
              break
            case 'Costo de ventas':
              this.ledgerAccountEdit.ledgerAccountType.type = 'expenses'
              this.ledgerAccountEdit.ledgerAccountType.subType =
                'costofGoodsSold'
              break
            case 'Gastos':
              this.ledgerAccountEdit.ledgerAccountType.type = 'expenses'
              this.ledgerAccountEdit.ledgerAccountType.subType = 'expenses'
              break
            case 'Depreciacin':
              this.ledgerAccountEdit.ledgerAccountType.type = 'expenses'
              this.ledgerAccountEdit.ledgerAccountType.subType = 'depreciation'
              break
            case 'Otros Gastos':
              this.ledgerAccountEdit.ledgerAccountType.type = 'expenses'
              this.ledgerAccountEdit.ledgerAccountType.subType = 'otherExpenses'
              break
            case 'Orden':
              this.ledgerAccountEdit.ledgerAccountType.type = 'order'
              this.ledgerAccountEdit.ledgerAccountType.subType = 'order'
              break
          }
        }
      },
    },
  },
}
</script>

<template>
  <GxForm class="flex flex-col" @submit="EditLedgerAccount">
    <div class="flex flex-col w-98 h-124 border p-4 bg-white rounded-lg"
      ><div class="font-black text-2xl p-3 pl-4 mb-3"
        ><span class="font-black"
          >{{
            isRoot ? 'Modificar cuenta de mayor' : 'Modificar cuenta contable'
          }}
        </span></div
      >
      <div v-if="!isRoot"
        ><div class="flex flex-col ml-4 mb-2"
          ><span>Cuenta a la que pertenece</span></div
        >
        <div class="flex flex-col ml-4 w-82 overflow-hidden">
          <GxLedgerAccountsList
            :ledgerAccountsProp="ledgerAccountsParents"
            :editCatalog="false"
            :showCollapsed="true"
            :showArrowCollapse="false"
            :showHover="false"
          />
          <span class="ml-7 mt-1"
            ><span class="font-bold"> Tipo: {{ typeParent }}</span></span
          >
          <span class="ml-7"
            >Naturaleza:
            <span class="font-bold"> {{ natureParent }}</span></span
          >
        </div>
      </div>

      <GxTextFieldNumeric
        v-model="ledgerAccountEdit.shortNumber"
        required
        class="px-4"
        :name="getLavelNumber"
        :label="getLavelNumber"
        autofocus
        disabled
      />
      <GxTextField
        v-model="ledgerAccountEdit.name"
        required
        class="px-4 mt-2"
        name="Nombre de la cuenta"
        label="Nombre de la cuenta"
        autofocus
      />
      <GxDropdown
        v-if="isRoot"
        class="flex flex-col mt-2"
        v-model="typeOption"
        :options="listTypes"
        placeholder="Tipo"
        label="Tipo"
        :allow-empty="false"
      />
      <!-- <div v-if="isRoot" class="flex flex-col ml-4 -mt-3">
        <div><span>Naturaleza</span></div>
        <div class="flex mt-2">
          <div
            class="flex border rounded-lg block w-15 text-2xs cursor-pointer hover:border-gray-500"
            :class="{
              'border-primary bg-secondary-50':
                ledgerAccountEdit.nature === 'debit',
            }"
            @click="changeNature('debit')"
            ><span class="ml-2"> Deudora</span></div
          >
          <div
            class="flex border ml-5 rounded-lg block w-15 text-2xs cursor-pointer hover:border-gray-500"
            :class="{
              'border-primary bg-secondary-50':
                ledgerAccountEdit.nature === 'credit',
            }"
            @click="changeNature('credit')"
            ><span class="ml-1"> Acreedora</span></div
          >
        </div>
      </div> -->

      <GxDropdown
        class="flex flex-col mt-3"
        v-model="satGroupCodeOption"
        :options="satGroupCode"
        placeholder="Cuenta agrupadora del SAT"
        label="Cuenta agrupadora del SAT"
        :allow-empty="false"
      />
      <div class="ml-5 -mt-3 text-alert" v-if="this.errorDescription">{{
        this.errorDescription
      }}</div>
      <div class="flex w-3/4 absolute mt-100">
        <div class="flex flex-grow w-full items-center content-center">
          <div
            class="flex ml-4 mr-6"
            v-show="!this.loading"
            :class="{ ' delay-300 duration-300 hidden': this.loading }"
          >
            <GxButton
              :variant="true"
              :ghost="true"
              class=" bottom-0 rounded-md gx-button"
              @click="onCloseEdit"
            >
              CANCELAR
            </GxButton>
          </div>
          <div
            class="flex"
            :class="{ ' delay-300 duration-300 pl-36': this.loading }"
          >
            <GxButton
              :variant="!this.loading"
              class=" bottom-0 gx-button"
              type="submit"
              :loading="loading"
            >
              Guardar
            </GxButton>
          </div>
        </div>
      </div>
    </div>
  </GxForm>
</template>
