<script>
import GxButtonActions from '@components/_CoreTaxes/Buttons/ButtonActions/GxButtonActions'
import GxButtonTaxes from '@components/_CoreTaxes/Buttons/Button/GxButtonTaxes'

import GxLedgerAccountsList from '@components/_CoreTaxes/LedgerAccounts/GxLedgerAccountsList'
import GxNewLedgerAccount from '@components/_CoreTaxes/LedgerAccounts/GxNewLedgerAccount'
import GxEditLedgerAccount from '@components/_CoreTaxes/LedgerAccounts/GxEditLedgerAccount'
import GxDeleteLedgerAccount from '@components/_CoreTaxes/LedgerAccounts/GxDeleteLedgerAccount'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'

import { GET_LEDGER_ACCOUNTS_ELECTRONIC_ACCOUNTING } from '@src/apollo/queries'

// import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
import GxModal from '@components/_Core/Modals/GxModal'

export default {
  name: 'GxLedgerAccounts',
  data() {
    return {
      showModelCreate: false,
      showModelEdit: false,
      showModelDelete: false,
      isRoot: false,
      accountingFirmNumber: null,
      clientNumber: null,
      errorDescription: '',
      removeLedgerAccount: null,
      editBeginningBalancesConfig: false,
      downloadingLedgerAccountsXML: true,
    }
  },
  components: {
    GxLedgerAccountsList,
    GxNewLedgerAccount,
    GxEditLedgerAccount,
    GxDeleteLedgerAccount,
    GxModal,
    GxCardModal,
    GxButtonActions,
    GxButtonTaxes,
  },
  apollo: {
    getLedgerAccountsElectronicAccounting: {
      query: GET_LEDGER_ACCOUNTS_ELECTRONIC_ACCOUNTING,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
          periodId: Number(
            new Date().getFullYear() + ('0' + new Date().getMonth()).slice(-2)
          ),
        }
      },
      update(data) {
        window.open(
          data.myAccountingFirms[0].clients[0].accounting.reports
            .additionalReportsLocalized.contabilidadElectronicaCatalogoDeCuentas
            .url,
          'Download'
        )
        this.downloadingLedgerAccountsXML = true
      },
      skip() {
        return this.downloadingLedgerAccountsXML
      },
      fetchPolicy: 'no-cache',
    },
  },
  props: {
    accountingFirmNumberProp: {
      type: String,
      default: null,
    },
    clientNumberProp: {
      type: String,
      default: null,
    },
    ledgerAccountsProp: {
      type: Array,
      default: null,
    },
    editCatalog: {
      type: Boolean,
      default: false,
    },
    editBeginningBalances: {
      type: Boolean,
      default: false,
    },
    showCollapsed: {
      type: Boolean,
      default: false,
    },
    showArrowCollapse: {
      type: Boolean,
      default: false,
    },
    isInitial: {
      type: Boolean,
      default: false,
    },
    beginningPeriodId: {
      type: Number,
      default: null,
    },
    endingPeriodId: {
      type: Number,
      default: null,
    },
  },
  created() {
    this.accountingFirmNumber = this.accountingFirmNumberProp
    this.clientNumber = this.clientNumberProp
  },
  computed: {
    ledgerAccounts() {
      return this.ledgerAccountsProp
    },
    getButtonsActions() {
      let actions = [
        {
          name: this.editBeginningBalancesConfig
            ? 'Ocultar saldos iniciales'
            : 'Mostrar saldos iniciales',
          icon: !this.editBeginningBalancesConfig
            ? 'visibility'
            : 'visibility_off',
          type: 'method',
          action: 'showBeginningBalances',
        },
        {
          name: 'Descargar el catlogo de cuentas en Excel',
          icon: 'cloud_download',
          type: 'method',
          action: 'downloadExcel',
        },
        {
          name:
            'Descargar el catlogo de cuentas en XML para contabilidad electrnica',
          icon: 'cloud_download',
          type: 'method',
          action: 'downloadXML',
        },
      ]
      return actions
    },
    getNamePeriod() {
      let months = [
        'Enero',
        'Febrero',
        'Marzo',
        'Abril',
        'Mayo',
        'Junio',
        'Julio',
        'Agosto',
        'Septiembre',
        'Octubre',
        'Noviembre',
        'Diciembre',
      ]

      return months[Number(this.beginningPeriodId.toString().slice(4)) - 1]
    },
    getYear() {
      return this.beginningPeriodId.toString().slice(0, 4)
    },
  },
  watch: {
    immediate: true,
  },
  methods: {
    createMajorLedgerAccount() {
      this.isRoot = true
      this.ledgerAccountParent = null
      this.showModelCreate = true
    },
    createSubLedgerAccount(ledgerAccountParent) {
      this.isRoot = false
      this.ledgerAccountParent = ledgerAccountParent
      this.showModelCreate = true
      //console.log(ledgerAccountParent)
    },
    editMajorLedgerAccount() {
      this.isRoot = true
      this.showModelEdit = true
      // console.log('editMajorLedgerAccount')
    },
    editLedgerAccount(ledgerAccountParent) {
      this.isRoot = ledgerAccountParent.subAccounts === null ? true : false
      this.showModelEdit = true
      this.ledgerAccountParent = ledgerAccountParent
    },
    deleteLedgerAccount(deleteLedgerAccount) {
      this.removeLedgerAccount = deleteLedgerAccount
      //console.log(this.removeLedgerAccount)
      this.showModelDelete = true
    },
    onCloseDelete() {
      this.showModelDelete = false
    },
    onDelete(ledgerAccountId) {
      this.showModelDelete = false
      this.searchToRemove(this.ledgerAccounts, ledgerAccountId)
    },
    onCreated(ledgerAccount) {
      this.showModelCreate = false
      console.log(ledgerAccount)
      this.searchToAdd(this.ledgerAccounts, ledgerAccount)
    },
    onUpdate(ledgerAccount) {
      this.showModelEdit = false
      //console.log(ledgerAccount)
      this.searchToRemplace(this.ledgerAccounts, ledgerAccount)
    },
    searchToRemove(ledgerAccounts, ledgerAccountId) {
      for (var index = 0; index < ledgerAccounts.length; index++) {
        if (ledgerAccounts[index].ledgerAccountId === ledgerAccountId) {
          ledgerAccounts.splice(index, 1)
          return true
        }
        if (ledgerAccounts[index].subAccounts !== null) {
          if (
            this.searchToRemove(
              ledgerAccounts[index].subAccounts,
              ledgerAccountId
            )
          )
            return true
        }
      }
    },
    searchToAdd(ledgerAccounts, ledgeraccount) {
      let pushed = false
      if (ledgeraccount.parent !== null) {
        for (var index = 0; index < ledgerAccounts.length; index++) {
          if (ledgerAccounts[index].ledgerAccountId === ledgeraccount.parent) {
            if (ledgerAccounts[index].subAccounts === null) {
              ledgerAccounts[index].subAccounts = [ledgeraccount]
              pushed = true
            } else if (ledgerAccounts[index].subAccounts.length === 0) {
              ledgerAccounts[index].subAccounts = [ledgeraccount]
              pushed = true
            } else {
              ledgerAccounts[index].subAccounts.forEach(
                (subledgerAccount, subIndex) => {
                  if (
                    subledgerAccount.shortNumber > ledgeraccount.shortNumber
                  ) {
                    pushed = true
                    ledgerAccounts[index].subAccounts.splice(
                      subIndex,
                      0,
                      ledgeraccount
                    )
                  }
                }
              )
            }
            if (!pushed) ledgerAccounts[index].subAccounts.push(ledgeraccount)
            return true
          }
          if (ledgerAccounts[index].subAccounts !== null) {
            if (
              this.searchToAdd(ledgerAccounts[index].subAccounts, ledgeraccount)
            )
              return true
          }
        }
      } else {
        for (
          var indexParent = 0;
          indexParent < ledgerAccounts.length;
          indexParent++
        ) {
          if (
            ledgerAccounts[indexParent].shortNumber > ledgeraccount.shortNumber
          ) {
            pushed = true
            ledgerAccounts.splice(indexParent, 0, ledgeraccount)
            break
          }
        }
        if (!pushed) ledgerAccounts.push(ledgeraccount)
      }
    },
    searchToRemplace(ledgerAccounts, ledgeraccount) {
      //ledgerAccounts.forEach((ledgerAccount, index) => {
      for (var index = 0; index < ledgerAccounts.length; index++) {
        if (
          ledgerAccounts[index].ledgerAccountId ===
          ledgeraccount.ledgerAccountId
        ) {
          ledgerAccounts[index].number = ledgeraccount.number
          ledgerAccounts[index].shortNumber = ledgeraccount.shortNumber
          ledgerAccounts[index].name = ledgeraccount.name
          ledgerAccounts[index].nature = ledgeraccount.nature
          ledgerAccounts[index].ledgerAccountType.type =
            ledgeraccount.ledgerAccountType.type
          ledgerAccounts[index].ledgerAccountType.subType =
            ledgeraccount.ledgerAccountType.subType
          ledgerAccounts[index].ledgerAccountType.subSubType =
            ledgeraccount.ledgerAccountType.subSubType
          ledgerAccounts[index].additionalDataLocalized =
            ledgeraccount.additionalDataLocalized
          return true
        }
        if (ledgerAccounts[index].subAccounts != null) {
          if (
            this.searchToRemplace(
              ledgerAccounts[index].subAccounts,
              ledgeraccount
            )
          )
            return true
        }
      }
      //})
    },
    onCloseCreate() {
      //alert('CreateHidden')
      this.showModelCreate = false
    },
    onCloseEdit() {
      //alert('CreateHidden')
      this.showModelEdit = false
    },
    notificationError(errors) {
      this.errorDescription = errors
    },
    closeModel() {
      this.errorDescription = ''
    },
    finishRender() {
      this.$emit('finishRender')
    },
    showBeginningBalances() {
      // console.log('mostrar')
      this.editBeginningBalancesConfig = !this.editBeginningBalancesConfig
    },
    downloadExcel() {},
    downloadXML() {
      this.downloadingLedgerAccountsXML = false
    },
  },
}
</script>

<template>
  <div class="font-nunito">
    <div class="flex flex-col">
      <div class="flex inline-flex mb-8 text-white">
        <div class="pl-2 pr-2 rounded h-5 mr-4 bg-ledgerAccountTypes-assets"
          ><span class="font-bold">Activo</span></div
        >
        <div
          class="pl-2 pr-2 h-5 mr-4 rounded bg-ledgerAccountTypes-liabilities"
          ><span class="font-bold">Pasivo</span></div
        >
        <div class="pl-2 pr-2 h-5 mr-4 rounded bg-ledgerAccountTypes-capital"
          ><span class="font-bold">Capital</span></div
        >
        <div class="pl-2 pr-2 h-5 mr-4 rounded bg-ledgerAccountTypes-revenue"
          ><span class="font-bold">Resultados</span></div
        >
        <div class="pl-2 pr-2 h-5 mr-4 rounded bg-ledgerAccountTypes-order"
          ><span class="font-bold">Orden</span></div
        >
        <div
          v-if="!this.editBeginningBalances && this.isInitial"
          class="flex flex-col absolute right-0 -mt-7"
        >
          <GxButton
            class="mr-10 mt-4 gx-button"
            @click="createMajorLedgerAccount"
          >
            + CUENTA de MAYOR
          </GxButton>
        </div>

        <div v-if="!this.isInitial" class="flex absolute right-0 -mt-1.5 mr-19">
          <div class="flex -mt-4 mr-2 w-61 items-start">
            <GxButtonTaxes
              :iconSize="'16px'"
              :icon="'add_circle_outline'"
              v-bind:style="{ fontSize: '14px' }"
              class="ml-10 mt-4 bottom-0 rounded-md gx-button"
              @click="createMajorLedgerAccount"
            >
              Agregar cuenta de mayor
            </GxButtonTaxes>
          </div>
          <div>
            <!-- @downloadPDF="downloadPDF" -->

            <GxButtonActions
              :buttonsActions="getButtonsActions"
              @showBeginningBalances="showBeginningBalances"
              @downloadExcel="downloadExcel"
              @downloadXML="downloadXML"
          /></div>
        </div>
      </div>

      <div
        v-if="!isInitial"
        class="flex border-b text-gray-700 w-full justify-between bg-gray-100 mb-2"
      >
        <div class="flex">
          <div class="flex w-auto items-center h-6">
            <span class="font-bold pl-4">
              Cuentas
            </span>
          </div>
        </div>
        <div v-if="editBeginningBalancesConfig" class="flex">
          <div class="flex w-39 h-6 items-center justify-center">
            <span class="font-bold">
              Saldo Inicial {{ getNamePeriod }} {{ getYear }}
            </span>
          </div>

          <div class="flex w-23 h-6 items-center justify-center">
            <span class="font-bold">
              Naturaleza
            </span>
          </div>
        </div>
      </div>
      <!-- <ul class="pb-2 rounded-lg shadow-2"> -->
      <GxLedgerAccountsList
        :accountingFirmNumberProp="accountingFirmNumber"
        :clientNumberProp="clientNumber"
        :editBeginningBalances="
          editBeginningBalances || editBeginningBalancesConfig
        "
        :ledgerAccountsProp="ledgerAccounts"
        :editCatalog="editCatalog"
        :showCollapsed="showCollapsed"
        :showArrowCollapse="showArrowCollapse"
        :showHover="true"
        :isRootProp="true"
        :isInitial="isInitial"
        @editLedgerAccount="editLedgerAccount"
        @createSubLedgerAccount="createSubLedgerAccount"
        @deleteLedgerAccount="deleteLedgerAccount"
        @finishRender="finishRender"
      />

      <!-- </ul> -->
    </div>

    <GxModal
      v-if="showModelEdit"
      :delay-animation="true"
      :overflowVisible="false"
    >
      <transition name="grow" appear>
        <GxEditLedgerAccount
          :isRoot="this.isRoot"
          @onCloseEdit="onCloseEdit"
          @onUpdate="onUpdate"
          :ledgerAccountsParentsProp="[ledgerAccountParent]"
          :accountingFirmNumberProp="accountingFirmNumber"
          :clientNumberProp="clientNumber"
        />
      </transition>
    </GxModal>

    <GxModal
      v-if="showModelCreate"
      :delay-animation="true"
      :overflowVisible="false"
    >
      <transition name="grow" appear>
        <GxNewLedgerAccount
          :isRoot="isRoot"
          @onCloseCreate="onCloseCreate"
          @onCreated="onCreated"
          :ledgerAccountsParentsProp="[ledgerAccountParent]"
          :accountingFirmNumberProp="accountingFirmNumber"
          :clientNumberProp="clientNumber"
          @notificationError="notificationError"
        />
      </transition>
    </GxModal>

    <GxModal
      v-if="showModelDelete"
      :delay-animation="true"
      :overflowVisible="false"
    >
      <transition name="grow" appear>
        <GxDeleteLedgerAccount
          @onCloseDelete="onCloseDelete"
          :deleteLedgerAccountProp="removeLedgerAccount"
          :accountingFirmNumberProp="accountingFirmNumber"
          :clientNumberProp="clientNumber"
          @onDelete="onDelete"
        />
      </transition>
    </GxModal>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Error al tratar de crear la cuenta contable'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModel">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </div>
</template>
