<script>
import GxIcon from '@components/_Core/Icons/GxIcon'
import {
  LEDGER_ACCOUNT_UPDATE,
  CONFIG_SET_BEGINNING_BALANCE,
} from '@src/apollo/mutations'
import Big from 'big.js'

export default {
  name: 'GxLedgerAccountsListItem',
  data() {
    return {
      accountingFirmNumber: null,
      clientNumber: null,
      expand: false,
      showInputEdit: false,
      previousName: '',
      childrensBeginningBalance: new Array(),
      childrenNumber: null,
      showInputBeginningBalance: false,
    }
  },
  components: {
    GxIcon,
    GxLedgerAccountsList: () =>
      //   // Debido a que son componentes recursivos marca error al importar por que genera una dependencia de bucle,
      //   // por lo que la solucin fue agregar una importacin dinamica, se reviso con Jhonatan
      import('@components/_CoreTaxes/LedgerAccounts/GxLedgerAccountsList'),
  },
  created() {
    this.expand = this.showCollapsed
    this.accountingFirmNumber = this.accountingFirmNumberProp
    this.clientNumber = this.clientNumberProp
    this.childrenNumber = this.childrenNumberProp
    //console.log(this.ledgerAccount)
    if (this.ledgerAccount.parent !== null) {
      if (
        (this.ledgerAccount.subAccounts ?? []).length === 0 &&
        this.editBeginningBalances
      ) {
        // setTimeout(() => {
        this.$emit(
          'recalculatedParent',
          this.ledgerAccount.beginningBalance,
          this.childrenNumber
        )
      }
    }
    if (this.editBeginningBalances)
      this.ledgerAccount.beginningBalance = this.getBinningBalanceInitial()
  },
  watch: {
    editBeginningBalances() {
      // console.log('lectura')
      if (this.ledgerAccount.parent !== null) {
        if (
          (this.ledgerAccount.subAccounts ?? []).length === 0 &&
          this.editBeginningBalances
        ) {
          // setTimeout(() => {
          this.$emit(
            'recalculatedParent',
            this.ledgerAccount.beginningBalance,
            this.childrenNumber
          )
        }
      }
    },
  },
  props: {
    childrenNumberProp: {
      type: Number,
      default: null,
    },
    ledgerAccountProp: {
      type: Object,
      default: null,
    },
    editCatalog: {
      type: Boolean,
      default: false,
    },
    editBeginningBalances: {
      type: Boolean,
      default: false,
    },
    showCollapsed: {
      type: Boolean,
      default: false,
    },
    showArrowCollapse: {
      type: Boolean,
      default: false,
    },
    showHover: {
      type: Boolean,
      default: true,
    },
    justThisItem: {
      type: Boolean,
      default: false,
    },
    accountingFirmNumberProp: {
      type: String,
      default: null,
    },
    clientNumberProp: {
      type: String,
      default: null,
    },
    isInitial: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ledgerAccount() {
      return this.ledgerAccountProp
    },
    getNumberOfAccountsByParts() {
      return this.ledgerAccount.number.split('-')
    },
    getSubAccountsCount() {
      if (this.ledgerAccount.subAccounts === undefined) return 0
      if (this.ledgerAccount.subAccounts === null) return 0
      return this.ledgerAccount.subAccounts.length
    },
    getNature() {
      return this.ledgerAccount.nature === 'D' ? 'Deudora' : 'Acreedora'
    },
    getCurrency() {
      return 'MXN'
    },
    getParentBeginningBalances() {
      let suma = new Big(0)
      this.childrensBeginningBalance.forEach(function(balance) {
        if (balance == '') balance = '0'
        suma = suma.plus(balance)
      })
      return this.formatDecimal(suma.toFixed(2).toString())
    },
  },
  methods: {
    getBinningBalanceInitial() {
      let value = this.formatDecimal(
        this.ledgerAccount.beginningBalance.toString()
      )
      //if (value === '0') this.saveBeginningBalance()
      return value == '0' ? '' : value
    },
    showSubAccounts() {
      if (!this.editBeginningBalances) {
        this.expand = !this.expand
        if (this.expand) {
          setTimeout(() => {
            this.validateViewSubList()
          }, 2000)
        }
      }
    },
    async onBlur(e) {
      this.hasFocus = false
      const value = this.formatDecimal(e.target.value.replace(/[^0-9.]/g, ''))
      e.target.value = value
      this.showInputBeginningBalance = false

      this.saveBeginningBalance()
      return
    },
    async saveBeginningBalance() {
      try {
        //const response =
        await this.$apollo.mutate({
          mutation: CONFIG_SET_BEGINNING_BALANCE,
          variables: {
            accountingFirmNumber: this.accountingFirmNumber,
            clientNumber: this.clientNumber,
            ledgerAccountId: this.ledgerAccount.ledgerAccountId,
            beginningBalance: this.ledgerAccount.beginningBalance,
          },
        })
      } catch (error) {
        console.log(error)
        return null
      }
    },
    onKeyDownBeginningBalance(e) {
      const keyCodes = [110, 8, 37, 39, 9, 46]
      if (e.altKey || e.ctrlKey || e.shiftKey) {
        e.preventDefault()
        e.stopPropagation()
        return
      }

      if (
        (e.keyCode >= 48 && e.keyCode <= 57) ||
        (e.keyCode >= 96 && e.keyCode <= 105) ||
        keyCodes.includes(e.keyCode)
      ) {
        if (e.keyCode == 110 && e.target.value.indexOf('.') > -1)
          e.preventDefault()
      } else {
        e.preventDefault()
      }
    },
    onKeyUpBegginningBalance(e) {
      e.target.value = e.target.value.replace(/(\.\d{6})\d+/g, '$1')
      if (this.ledgerAccount.parent !== null) {
        this.$emit('recalculatedParent', e.target.value, this.childrenNumber)
      }
    },
    editMajorLedgerAccount() {
      this.$emit('editMajorLedgerAccount')
    },
    formatDecimal(text) {
      // console.log(text)
      // console.log(text === '0.00')
      if (text.includes('.')) {
        if (text == '0') return ''
        if (text === '0.00') return ''
        return text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\d(?=(\d{3})+\.)/g, '$&,')
      } else {
        //return (
        text = text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\.\d$/, '0$&0')
          .replace(/\d+\.$/, '$&00')
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
        if (text == '0') return ''
        if (text == '0' || text == '') return text
        return text + '.00'
        //)
      }
    },
    KeyPressformatDecimal(text) {
      if (text.includes('.')) {
        // console.log(text)
        // console.log(text === '0.00')
        if (text === '0.00') return ''
        return text.replace(/\d(?=(\d{3})+\.)/g, '$&,')
      } else {
        text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\.\d$/, '0$&0')
          .replace(/\d+\.$/, '$&00')
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
        if (text == '0') return ''
        if (text == '0' || text == '') return text
        return text + '.00'
      }
    },
    editLedgerAccount(ledgerAccountParent) {
      let auxLedgerAccountParent = JSON.parse(
        JSON.stringify(this.ledgerAccount)
      )
      auxLedgerAccountParent.subAccounts =
        ledgerAccountParent === null ? null : [ledgerAccountParent]
      //console.log(auxLedgerAccountParent)
      this.$emit('editLedgerAccount', auxLedgerAccountParent)
    },
    createSubLedgerAccount(ledgerAccountParent) {
      let auxLedgerAccountParent = JSON.parse(
        JSON.stringify(this.ledgerAccount)
      )
      auxLedgerAccountParent.subAccounts =
        ledgerAccountParent === null ? null : [ledgerAccountParent]
      // console.log(auxLedgerAccountParent)
      this.$emit('createSubLedgerAccount', auxLedgerAccountParent)
    },
    deleteLedgerAccount(deleteLedgerAccount) {
      // console.log(deleteLedgerAccount)
      this.$emit('deleteLedgerAccount', deleteLedgerAccount)
    },
    editName() {
      if (this.editCatalog) {
        this.showInputEdit = true
        this.previousName = this.ledgerAccount.name
      }
      this.timeout = setTimeout(() => {
        this.$refs.gxInput.focus()
      }, 100)
      this.$once('hook:beforeDestroy', () => {
        if (this.timeout) {
          clearTimeout(this.timeout)
        }
      })
    },
    focusoutEditName() {
      this.showInputEdit = false
    },
    async enterEditName() {
      this.previousName = ''
      this.showInputEdit = false
      this.EditLedgerAccount()
    },
    escEditName() {
      this.ledgerAccount.name = this.previousName
      this.showInputEdit = false
    },
    recalculatedParent(amount, childrenN) {
      let amountNumber = (amount + '').replace(',', '')

      this.$set(this.childrensBeginningBalance, childrenN, amountNumber)
      if (this.ledgerAccount.parent !== null) {
        this.$emit(
          'recalculatedParent',
          this.getParentBeginningBalances,
          this.childrenNumber
        )
      }
    },
    getPositionTop() {
      let element = this.$refs.subList
      //console.log(element)

      var offset = 0
      while (element) {
        offset += element['offsetTop']
        element = element.offsetParent
      }
      return offset
    },
    validateViewSubList() {
      let elt = this.$refs.subList
      if (!elt) {
        // Element not found.
        return false
      }
      // Get the top and bottom position of the given element.
      var posTop = this.getPositionTop(elt)
      // console.log(posTop)

      var posBottom = posTop + elt.offsetHeight
      // Get the top and bottom position of the *visible* part of the window.
      var visibleTop = document.body.scrollTop
      var visibleBottom = visibleTop + document.documentElement.offsetHeight
      return posBottom >= visibleTop && posTop <= visibleBottom
    },
    async EditLedgerAccount() {
      try {
        await this.$apollo.mutate({
          mutation: LEDGER_ACCOUNT_UPDATE,
          variables: {
            accountingFirmNumber: this.accountingFirmNumber,
            clientNumber: this.clientNumber,
            ledgerAccountId: this.ledgerAccount.ledgerAccountId,
            input: {
              number: this.ledgerAccount.number,
              name: this.ledgerAccount.name,
              type: this.ledgerAccount.ledgerAccountType.type,
              subType: this.ledgerAccount.ledgerAccountType.subType,
              subSubType: this.ledgerAccount.ledgerAccountType.subSubType,
              nature: this.ledgerAccount.nature,
              // additionalDataLocalized: {
              //   codigoAgrupadorDelSat: this.ledgerAccountEdit
              //     .additionalDataLocalized.codigoAgrupadorDelSat,
              // },
            },
          },
        })
        //console.log(response)
      } catch (error) {
        console.log(error)
        return null
      }
    },
    onFocusBeginningBalance(e) {
      e.target.value = e.target.value.replace(',', '')
    },
    showBeginningBalance() {
      this.showInputBeginningBalance = !this.showInputBeginningBalance
      this.timeout = setTimeout(() => {
        this.$refs.inputBeginningBalance.focus()
        this.$refs.inputBeginningBalance.select()
      }, 100)
      this.$once('hook:beforeDestroy', () => {
        if (this.timeout) {
          clearTimeout(this.timeout)
        }
      })
    },
    hideBeginningBalance() {
      this.showInputBeginningBalance = false
    },
  },
}
</script>

<template>
  <li class="">
    <div
      class="group flex align-middle rounded "
      :class="{
        'cursor-pointer': getSubAccountsCount > 0,
        'pt-1 pb-1 hover:bg-ledgerAccountlist-hover': showHover,
        'mb-1': !showHover,
      }"
      @click="showSubAccounts"
    >
      <div
        class="inline-flex w-4 pl-1 font-bold"
        :class="[
          'text-ledgerAccountTypes-' + ledgerAccount.ledgerAccountType.type,
        ]"
      >
        {{ ledgerAccount.parent === null ? 'M' : ' ' }}</div
      >
      <div v-if="showArrowCollapse" class="inline-flex w-4 ml-3"
        ><GxIcon
          v-if="getSubAccountsCount > 0"
          class="flex justify-end flex-grow w-4 h-4 pt-px pr-1.3px border border-solid rounded-full cursor-pointer border-ledgerAccountlist-arrowBorder text-list-arrowBorder"
          :icon="!expand ? 'chevron_right' : 'expand_less'"
          :class="{ 'bg-ledgerAccountList-arrow': !expand }"
      /></div>
      <div
        class="inline-flex text-white ml-3"
        :class="{
          'ml-3': showArrowCollapse && getSubAccountsCount > 0,
          'ml-4': showArrowCollapse && getSubAccountsCount === 0,
        }"
      >
        <span
          class="pt-1 pb-2px mr-px font-bold text-2xs"
          v-for="(item, idx) in getNumberOfAccountsByParts"
          :class="[
            'bg-ledgerAccountTypes-' + ledgerAccount.ledgerAccountType.type,
            {
              'rounded-tl rounded-bl pl-2 pr-1': idx === 0,
              'pl-1 pr-2 rounded-r':
                idx === getNumberOfAccountsByParts.length - 1 &&
                !getSubAccountsCount > 0,
              'pl-2 pr-2':
                (idx !== 0 && idx !== getNumberOfAccountsByParts.length - 1) ||
                (idx !== 0 &&
                  idx == getNumberOfAccountsByParts.length - 1 &&
                  getSubAccountsCount > 0),
            },
          ]"
          :key="idx"
        >
          {{ item }}
        </span>
      </div>
      <div class="inline-flex pl-5 pt-2px sm:hover:bg-secondary-20 w-full">
        <div
          class="inline-flex"
          :class="{ 'w-full': this.showInputEdit }"
          @click.prevent.stop="editName"
          :ledgerAccountId="ledgerAccount.ledgerAccountId"
        >
          <span
            v-show="!this.showInputEdit"
            class="cortar"
            :class="{
              'font-bold': getSubAccountsCount > 0,
            }"
          >
            {{ ledgerAccount.name }}</span
          >
          <!-- ref="nameLedgerAccount" -->

          <input
            class="w-full mr-5 pl-2 pt-px pb-px rounded-sm"
            v-show="this.showInputEdit"
            v-model="ledgerAccount.name"
            ref="gxInput"
            @focusout.prevent.stop="enterEditName"
            @keydown.enter.prevent.stop="enterEditName"
            @keydown.esc.prevent.stop="escEditName"
          />
        </div>

        <!-- Edit -->
        <div
          v-if="editCatalog && !this.showInputEdit"
          class="text-ledgerAccountlist-editItems -mb-1"
        >
          <div class="pl-5 pr-5 hidden group-hover:block"
            ><GxIcon
              :icon="'edit'"
              class="cursor-pointer"
              v-bind:style="{ fontSize: '18px' }"
              :title="'Editar'"
              @click.prevent.stop="editLedgerAccount(null)"
            />
            <GxIcon
              :icon="'library_add'"
              class="pl-2 cursor-pointer"
              v-bind:style="{ fontSize: '18px' }"
              @click.prevent.stop="createSubLedgerAccount(null)"
            />
            <GxIcon
              :icon="'delete'"
              class="pl-2 cursor-pointer"
              v-bind:style="{ fontSize: '18px' }"
              @click.prevent.stop="deleteLedgerAccount(ledgerAccount)"
            />
          </div>
        </div>
        <!-- End Edit -->

        <!-- BeginningBalances -->
        <div
          class="flex h-5 right-0 absolute w-86 justify-between items-stretch"
          v-if="
            this.editBeginningBalances &&
              this.isInitial &&
              !this.showInputBeginningBalance
          "
        >
          <div class="flex flex-col w-46 font-black text-right pr-3">
            <span v-if="getSubAccountsCount > 0" class="font-black pr-1">
              $
              {{
                getParentBeginningBalances === ''
                  ? '0.00'
                  : getParentBeginningBalances
              }}</span
            >
            <!-- @keyup="this.keyUpTxtBeginningBalances" -->
            <!-- @blur="onBlur" -->
            <!-- pattern="^\d*(\.\d{0,2})?$" -->

            <input
              v-else
              type="text"
              step="0.01"
              v-model="ledgerAccount.beginningBalance"
              class="border rounded-md text-right pr-1"
              @keydown="this.onKeyDownBeginningBalance"
              @keyup="this.onKeyUpBegginningBalance"
              @blur="this.onBlur"
              @focus="this.onFocusBeginningBalance"
              inputmode="decimal"
              @onclick="this.select()"
            />
          </div>
          <div class="flex flex-col w-15 font-bold">{{ this.getCurrency }}</div>
          <div class="flex flex-col w-18 font-bold mr-5">{{
            this.getNature
          }}</div>
        </div>

        <div
          v-else-if="this.editBeginningBalances"
          class="flex h-5 right-0 absolute w-86 justify-between items-stretch"
          :initial="ledgerAccount.beginningBalance"
        >
          <div class="flex flex-col w-46 font-black text-right">
            <span v-if="getSubAccountsCount > 0" class="font-black pr-2">
              {{
                getParentBeginningBalances === ''
                  ? '0.00'
                  : getParentBeginningBalances
              }}</span
            >
            <!-- @keyup="this.keyUpTxtBeginningBalances" -->
            <!-- @blur="onBlur" -->
            <!-- pattern="^\d*(\.\d{0,2})?$" -->
            <span
              v-else-if="
                getSubAccountsCount === 0 &&
                  !this.isInitial &&
                  !this.showInputBeginningBalance
              "
              class="pr-2"
            >
              {{
                ledgerAccount.beginningBalance
                  ? formatDecimal(ledgerAccount.beginningBalance.toString())
                  : '-'
              }}</span
            >
            <input
              ref="inputBeginningBalance"
              v-else
              type="text"
              step="0.01"
              v-model="ledgerAccount.beginningBalance"
              class="border rounded-md text-right pr-2"
              @keydown="this.onKeyDownBeginningBalance"
              @keyup="this.onKeyUpBegginningBalance"
              @blur="this.onBlur"
              @focus="this.onFocusBeginningBalance"
              inputmode="decimal"
              @onclick="this.select()"
            />
          </div>
          <div class="flex w-5 pl-2 pr-11 items-center">
            <!-- text-gray-500 -->
            <GxIcon
              v-if="!this.showInputEdit && getSubAccountsCount === 0"
              :icon="'edit'"
              class="cursor-pointer text-ledgerAccountlist-editItems -mt-1 hidden group-hover:block"
              v-bind:style="{ fontSize: '18px' }"
              :title="'Editar'"
              @click="showBeginningBalance"
            />
          </div>
          <div
            v-if="!this.showInputEdit"
            class="flex flex-col w-18 font-bold mr-22"
            >{{ this.getNature }}</div
          >
        </div>
        <!-- End BeginningBalances -->
      </div>
    </div>

    <transition name="slide" v-if="getSubAccountsCount > 0">
      <div v-show="expand && !justThisItem">
        <GxLedgerAccountsList
          :accountingFirmNumberProp="accountingFirmNumber"
          :clientNumberProp="clientNumber"
          :ledgerAccountsProp="
            ledgerAccount.subAccounts === null ? [] : ledgerAccount.subAccounts
          "
          :key="ledgerAccount.ledgerAccountId"
          :name="ledgerAccount.ledgerAccountId"
          :editCatalog="editCatalog"
          :showCollapsed="showCollapsed"
          :showArrowCollapse="showArrowCollapse"
          :editBeginningBalances="editBeginningBalances"
          :showHover="showHover"
          :isInitial="isInitial"
          @editLedgerAccount="editLedgerAccount"
          @createSubLedgerAccount="createSubLedgerAccount"
          @deleteLedgerAccount="deleteLedgerAccount"
          @recalculatedParent="recalculatedParent"
          :justThisItem="justThisItem"
        />
        <div ref="subList"></div>
      </div>
    </transition>
  </li>
</template>

<style scoped>
.slide-enter-active {
  transition-duration: 0.1s;
  transition-timing-function: ease-in;
}

.slide-leave-active {
  transition-duration: 0.1s;
  transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}

.slide-enter-to,
.slide-leave {
  max-height: 100px;
  overflow: hidden;
}

.slide-enter,
.slide-leave-to {
  overflow: hidden;
  max-height: 0;
}
.hideText {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-wrap: break-word;
}

.cortar {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
</style>
